.page-header__wrapper {
  padding: 30px 0 14px 0;
  border-bottom: 1px solid $line;

  @media (max-width: $tablet-width) {
    border: none;
    padding: 30px 0 30px 0;
  }

  @media (max-width: $mobile-width) {
    padding: 20px 0;
  }
}
.page-header__flex-wrapper {
  display: flex;
  justify-content: space-between;

  @media (max-width: $tablet-width) {
    align-items: center;
    flex-wrap: wrap;
  }
}
.page-header__user-navigation {
  padding-top: 11px;
  margin-left: 98px;

  @media (max-width: $tablet-width) {
    margin-left: 0;
    padding-top: 0;
  }
}
.page-header__user-navigation a {
  text-decoration: none;
  color: $font;
  font-weight: 500;
  font-size: 14px;
}
.page-header__user-navigation a:not(:last-child) {
  margin-right: 31px;
}
.page-header__logo {
  color: $brown;

  @media (max-width: $mobile-width) {
    width: 52px;
    height: 52px;
  }
}
.page-header__login {
  @media (max-width: $tablet-width) {
    display: none;
  }
}
.page-header__cart {
  white-space: nowrap;

  @media (max-width: $tablet-width) {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 37px;
    color: inherit;
  }

  @media (max-width: $mobile-width) {
    width: 20px;
  }
}
.page-header__cart-counter {
  margin-left: 3px;
}
.page-header__cart svg {
  display: none;

  @media (max-width: $tablet-width) {
    display: block;
  }
}
.page-header__cart span {
  @media (max-width: $tablet-width) {
    display: none;
  }
}
.page-header__menu-button {
  display: none;
  padding: 0 5px;
  color: font;
  border: none;
  color: inherit;
  background-color: transparent;

  @media (max-width: $tablet-width) {
    display: block;
  }

  @media (max-width: $mobile-width) {
    padding: 2px 0 0 0;
  }
}
.page-header--opened {
  @media (max-width: $tablet-width) {
    background-color: $brown;
    color: $white;
    padding-bottom: 133px;
    position: relative;
  }
}
.page-header--opened .page-header__wrapper {
  padding-bottom: 24px;
}
.page-header--opened .page-header__cart {
  @media (max-width: $tablet-width) {
    color: $white;
  }
}
.page-header--opened .page-header__logo {
  @media (max-width: $tablet-width) {
    color: white;
  }
}
.page-header--opened .search {
  @media (max-width: $tablet-width) {
    display: block;
    box-sizing: border-box;
    order: 4;
    flex: 1 0 100%;
    padding: 39px 4px 0;
  }

  @media (max-width: $mobile-width) {
    padding: 29px 0px 0;
  }
}
.page-header--opened .search input[type="search"] {
  @media (max-width: $tablet-width) {
    background-color: rgba($white, 0.15);
    color: $white;
    border-radius: 21px;
    box-sizing: border-box;
    width: 100%;
    padding: 13px 8px 13px 41px;
  }
}
.page-header--opened .search input[type="search"]::placeholder {
  @media (max-width: $tablet-width) {
    color: transparent;
  }
}
.page-header--opened .search__button {
  @media (max-width: $tablet-width) {
    color: inherit;
    top: initial;
    bottom: 0;
    left: 7px;
  }

  @media (max-width: $mobile-width) {
    left: 2px;
  }
}
.page-header--opened .search__button svg {
  @media (max-width: $tablet-width) {
    width: 16px;
    height: 16px;
  }
}
.page-header--opened .header-navigation {
  @media (max-width: $tablet-width) {
    display: block;
  }
}
.page-header--opened .header-navigation__list {
  @media (max-width: $tablet-width) {
    flex-direction: column;
    padding-top: 6px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 0;
  }
}
.page-header--opened .header-navigation__item {
  @media (max-width: $tablet-width) {
    margin-right: 0;
  }
}
.page-header--opened .header-navigation__link {
  @media (max-width: $tablet-width) {
    padding: 18px 0 18px 5px;
    color: inherit;
  font-size: 24px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 0;
  }
}
.page-header--opened .header-navigation-extra__item {
  @media (max-width: $tablet-width) {
    display: block;
  }
}
.page-header--opened .page-header__login {
  @media (max-width: $tablet-width) {
    display: block;
    position: absolute;
    left: 45px;
    bottom: 96px;
    color: inherit;
    font-size: 18px;
  }

  @media (max-width: $mobile-width) {
    left: 15px;
  }
}
