.slider {
  position: relative;
}
.slider__button {
  padding: 10px 0;
  background-color: transparent;
  border: none;
  color: $brown;
  display: flex;
  align-content: center;
  position: absolute;
  top: -54px;
}
.slider__button--back {
  left: 0;
}
.slider__button--next {
  right: 0;
}
.slider__button--back svg {
  transform: rotate(180deg);
}
.slider__list {
  display: grid;
  grid-template-columns: repeat(4, minmax(270px, 1fr));
  grid-auto-flow: column;
  column-gap: 30px;
  overflow: hidden;
  padding-top: 60px;
  margin-bottom: 8px;
}
.slider__item a {
  display: block;
  color: inherit;
  text-decoration: none;
}
.slider__item img {
  width: 100%;
  height: 284px;
  object-fit: cover;
  margin-bottom: 21px;
}
.slider__item h3 {
  font-weight: 500;
  font-size: 16px;
  text-align: center;
  margin: 0 0 6px;
}
.slider__item p {
  text-align: center;
  font-weight: 700;
  font-size: 14px;
  margin: 0;
}
.slider__pagination {
  display: flex;
  justify-content: center;
  font-weight: 500;
  color: $font-transparent;
}
.slider__pagination-item:not(:last-child) {
  margin-right: 13px;
}
.slider__pagination-item--current {
  color: $font;
}
