.promo {
  min-height: 463px;
  background-color: $brown;
  background-image: url("../img/content/main_promo_desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  color: $white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2px 0;
  box-sizing: border-box;
}
.promo__heading {
  text-transform: uppercase;
  font-family: $cormorant;
  font-weight: 700;
  font-size: 45px;
  letter-spacing: 1.3px;
  text-align: center;
  margin: 0 0 25px;
}
.promo__text-italic {
  font-family: $cormorant;
  text-align: center;
  font-style: italic;
  font-size: 38px;
  margin: 0;
}
