.social {
  padding-top: 6px;

  @media (max-width: $mobile-width) {
    display: flex;
    padding-top: 5px;
  }
}
.social__item {
  margin-bottom: 21px;

  @media (max-width: $mobile-width) {
    margin-bottom: 0;
    margin-right: 21px;
  }
}
.social__link {
  white-space: nowrap;
  display: flex;
  align-items: center;
}
.social__link svg {
  margin-right: 14px;

  @media (max-width: $mobile-width) {
    margin-right: 0;
    width: 25px;
    height: 25px;
  }
}
.social__link span {
  @media (max-width: $mobile-width) {
    display: none;
  }
}
