.footer-menu {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(4, min-content);
  grid-auto-flow: column;
  column-gap: 23%;
  margin-right: 11%;
  padding-top: 6px;

  @media (max-width: $tablet-width) {
    column-gap: 89px;
    margin-right: 35px;
  }

  @media (max-width: $mobile-width) {
    grid-template-columns: 1fr;
    margin-right: 0;
    column-gap: unset;
    grid-auto-flow: unset;
    flex: 1 0 100%;
  }
}
.footer-menu__item {
  margin-bottom: 21px;
}
.footer-menu__item:nth-child(4n) {
  margin-bottom: 40px;
}
.footer-menu__link {
  white-space: nowrap;
}
