.blog {
  text-align: center;
}
.blog .page__title {
  margin-bottom: 20px;
}
.blog .page__link {
  text-underline-offset: 7px;
}
.blog__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 30px;
  padding-top: 65px;
}
.blog__section {
  background-color: $bg-gray;
  grid-column: 1 / 2;
  min-height: 300px;
  display: flex;
  box-sizing: border-box;
}
.blog__section-big {
  background-color: $bg-gray;
  grid-column: 2 / 3;
  grid-row: 1 / 3;
}
.blog__image {
  flex: 1 1 50%;
}
.blog__image--care {
  background-image: url("../img/content/blog-care-desktop.jpg");
}
.blog__image--crystals {
  order: 2;
  background-image: url("../img/content/blog-crystals_desktop.jpg");
  background-position: right;
}
.blog__content {
  flex: 1 1 50%;
  padding: 65px 57px;
}
.blog-date {
  font-weight: 500;
  font-size: 14px;
  color: $font-transparent;
  margin: 0 0 12px;
}
.blog__content h3 {
  font-family: $cormorant;
  font-weight: 600;
  line-height: 130%;
  font-size: 26px;
  color: $black;
  margin: 0 0 37px;
}
